import { useState} from 'react'
import {Div, IconButton, Button, AppBar, P, CssBaseline, Drawer, Input, Toolbar} from '@thaysang/mui'
import Frame from 'react-frame-component';
import Editor from "@monaco-editor/react";
import {ErrorBoundary} from 'react-error-boundary'
import {useData} from './useData'
import useInterval from 'use-interval'

const App = () =>{
  const [data, setData] = useData({
    cur:  {name: "<untitled>",text: ""},
    files:{},
  })
  const [fontSize,setFontSize] = useState(22)
  const [code, setCode] = useState("")
  const [open, setOpen] = useState(false)
  const [isSave, setIsSave] = useState(false)

  function ErrorFallback({error, resetErrorBoundary}) {
   
    return (
      <Div role="alert">
        <P color="red" >Error:</P>
        <P color="red">{error.message}</P>
        <Button variant="outlined" onClick={resetErrorBoundary}>RESET</Button>
      </Div>
    )
  }

  const save = () => {
    if(data.cur.name !== "<untitled>") {
      data.files[data.cur.name] = data.cur.text
      setData({...data,files:data.files})
      setIsSave(true)
    }
  }

  useInterval(()=>{save()},5000)

  return (
    <Div flex={1} display="flex" flexDirection ="row" width="100vw" height="100vh" >
      <CssBaseline />
      <AppBar alignItems="center" bg="black" height={32} display="flex" flexDirection="row">
      <IconButton onClick={()=>{
        setOpen(!open)
      }} height={32} color="white" ml={1}> 📁 </IconButton>
       <IconButton onClick={()=>{
        save()
        console.log(data)
      }} height={32} color="white" ml={1} disabled={isSave}> 💾 </IconButton>
          <Input 
             value={data.cur.name}
             height = {30}
             color = "white"
             onChange={(e)=>{
              data.cur.name = e.target.value 
              setData({...data,cur: data.cur})
             }}
           />
           <IconButton onClick={async()=>{
            data.cur.name = await "<untitled>"
            data.cur.text = await ""
            await setData({...data,cur:data.cur})
            await setCode("")
            await setCode(data.cur.text)
      }} height={32} color="white" ml={1}> ✍️ </IconButton>
     <IconButton onClick={()=>{
        if (fontSize > 16) setFontSize(fontSize-1)
      }} height={32} color="white" ml={5}> - </IconButton> 
      <IconButton onClick={()=>{
        if (fontSize !== 22) setFontSize(22)
      }} height={32} color="white" ml={1}> = </IconButton>

      <IconButton onClick={()=>{
        if (fontSize < 50) setFontSize(fontSize+1)
      }} height={32} color="white" ml={1}> + </IconButton>
      
          <IconButton color="white" aria-label="RUN"
            ml = {20}
            height={32} width={64}
            onClick={async()=>{
              await setCode("")
              await console.clear()
              setCode(data.cur.text)
            }}
          > ▶ </IconButton>
      </AppBar>
      
      <Drawer open={open} anchor="left" width={200} onClick={()=>{
        setOpen(false)
      }}>
        <Div width={200} bg="black" flex={1}>
          <Toolbar variant="standard" height={32} bg="black">
            <P color="white">Files</P>
          </Toolbar>
          {
            Object.keys(data.files).map((item,index) =>
            <Div key={index} display="flex" flexDirection="row" justifyContent="space-between" alignItems="center"> 
              <Button onClick={async ()=>{
              data.cur.name = await item
              data.cur.text = await data.files[item]
              await setData({...data,cur:data.cur})
              await setOpen(false)
              }} ml={3} height={32} color="white" >{item}</Button>
              <IconButton color="white" aria-label="RUN"
                height={24} 
                ml={6}
                onClick={async()=>{
                  if(data.cur.name === item){
                    data.cur.name = await "<untitled>"
                    data.cur.text = await ""
                    await setCode("")
                    await setCode(data.cur.text)
                  }
                  await delete data.files[item]
                  await setData({...data,cur:data.cur, files:data.files})
                  await setOpen(false)
                }}
              > ❌</IconButton>
            </Div>
            )
          }
        </Div>
      </Drawer>
      <Div  flex={1} display="flex" marginTop={4}>
      <Editor
        defaultLanguage="javascript"
        theme='vs-dark'
        value={data.cur.text}
        options={{fontSize:fontSize}}
        onChange={ async (val) => {
          // setText(val)
          data.cur.text = await val 
          await setData({...data, cur:data.cur})
          setIsSave(false)
        }}
        />
      </Div>
      <Div flex={1 }display="flex" bg="white">
        <ErrorBoundary
          FallbackComponent={ErrorFallback}
          onReset={() => {
          }}
        >
          <Frame
            width="100%"
            height="100%"
          initialContent={
            `<html><head>
            <meta name="viewport" content="width=device-width, initial-scale=1.0" />
            <script type="text/javascript" src="https://cdn.jsdelivr.net/npm/@thaysang/realturtle"></script></head>
  <body>
    <canvas id="real-turtle"></canvas>
    <script type="text/javascript" src="https://unpkg.com/real-turtle/build/helpers/simple.js"></script>
    <script>
      const forward = (val) => {
        turtle.forward(val)
      }
      const backward = (val) => {
        turtle.back(val)
      }
      const right = (val) => {
        turtle.right(val)
      }
      const left = (val) => {
        turtle.left(val)
      }
      const circle = (radius, angle=360, counterclockwise=true) => {
        turtle.arc(radius, angle, counterclockwise)
      }
      const penColor = (val) => {
        turtle.setStrokeStyle(val)
      }
      const penSize = (val) => {
        turtle.setLineWidth(val)
      }
      
      const beginFill = (val) => {
        turtle.beginPath()
        turtle.setFillStyle(val)
      }
      const endFill = () => {
        turtle.fill()
      }

      const write = (val, font = '10px sans-serif') => {
        turtle.setFont(font)
        turtle.strokeText(val)
      }
      const seth = (val) => {
        turtle.setRotation(90-val)
      }
      
      const speed = (val) => {
        turtle.setSpeed(val)
      }
      const penup = () => {
        turtle.penUp()
      }
      const pendown = () => {
        turtle.penDown()
      }
      

      seth(0)
      const H_WIDTH = turtle.canvas.width/2 
      const H_HEIGHT = turtle.canvas.height/2
      const goto = (x,y) => {
        turtle.setPosition(x+H_WIDTH,-y+H_HEIGHT)
      }

      const dot = (r,mau) => {
        penup()
        backward(r/2)
        right(90)
        pendown()
        beginFill(mau)
        circle(r/2)
        endFill()
        penup()
        left(90)
        forward(r/2)
        pendown()
    }

    </script>
    <script type="text/javascript">
        ${code}
    </script>
  </body>
  </html>`
          }
          /> 
        </ErrorBoundary>
      </Div>

       
    </Div>
  )
}

export default App;
